import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { web.cbconnectit.portfolio.pages.HomePage() }
        ctx.router.register("/services") { web.cbconnectit.portfolio.pages.ServicesPage() }
        ctx.router.register("/services/{serviceId}") { web.cbconnectit.portfolio.pages.ServicePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.AboutImageStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.AboutTextStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.InputStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.LogoStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.MainImageStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.PortfolioArrowIconStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.ProjectNameStyle)
        ctx.theme.registerComponentStyle(web.cbconnectit.portfolio.styles.ServicesTypeCardStyle)
        ctx.theme.registerComponentVariants(web.cbconnectit.portfolio.components.PrimaryButtonVariant)
        ctx.theme.registerComponentVariants(web.cbconnectit.portfolio.components.NormalButtonVariant)
        ctx.theme.registerComponentVariants(web.cbconnectit.portfolio.components.TextPrimaryButtonVariant)
        web.cbconnectit.portfolio.updateTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(AppGlobalsLocal provides mapOf("title" to "Portfolio Web Compose"))
                {
            web.cbconnectit.portfolio.MyApp {
                router.renderActivePage { renderWithDeferred { it() } }
            }
        }
    }
}
