package web.cbconnectit.portfolio.models

import androidx.compose.runtime.Composable
import web.cbconnectit.portfolio.styles.primary
import web.cbconnectit.portfolio.svg.mobileDevelopmentSvg
import web.cbconnectit.portfolio.svg.overflowMenuSvg
import web.cbconnectit.portfolio.svg.tutoringSvg
import web.cbconnectit.portfolio.utils.Res
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.silk.theme.colors.palette.toPalette
import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable
import org.jetbrains.compose.web.css.CSSColorValue

@Serializable
data class Service(
    val id: String,
    val image: String,
    val title: String,
    @SerialName("short_description")
    val shortDescription: String,
    val description: String,
    @SerialName("banner_description")
    val bannerDescription: String,
    val type: ServiceType,
    @SerialName("sub_services")
    val subServices: List<SubService>? = null,
    @SerialName("extra_info")
    val extraInfo: String? = null
)

@Serializable
data class SubService(
    val title: String,
    val description: String,
    val image: String? = null,
    @SerialName("sub_type")
    val subType: ServiceSubType,
    val tag: Tag
)

enum class ServiceType(val image: String, val id: String, val path: String) {
    Mobile(Res.Image.servicesMobile, "mobile", "#mobile"),
    Web(Res.Image.servicesWeb, "web", "#web"),
    Tutoring(Res.Image.servicesTutoring, "tutoring", "#tutoring");

    @Composable
    fun getServiceTypeIcon(
        fill: CSSColorValue,
        modifier: Modifier = Modifier,
    ) {
        when (this) {
            Mobile -> mobileDevelopmentSvg(fill, modifier)
            Tutoring -> tutoringSvg(fill, modifier)
            Web -> overflowMenuSvg(fill, modifier)
        }
    }
}

enum class ServiceSubType {
    Android,
    iOS,
    KMP,
    Flutter,
    Placeholder
}

